.main {
  display: flex;
  width: 84%;
  margin-left: 8%;
  margin-top: 20px;
  justify-content: space-around;
}

.product {
  width: 45%;
  height: 300px;
  background-color: #e14a4f;
  border-radius: 20px;
  color: white;
}
.sale {
  width: 45%;
  height: 300px;
  background-color: #2d8ce6;
  border-radius: 20px;
  color: white;
}
h1 {
  margin-top: 50px;
  margin-left: 10%;
  width: 45%;
  text-align: left;
  font-size: 28px;
}
.dbskdms {
  margin-left: 10%;
  width: 50%;
  text-align: left;
}
.pz_img,
.cp_img {
  margin-top: -168px;
  margin-left: 10px;
  width: 30%;
  margin-left: 60%;
}

@media (max-width: 1200px) {
  .pz_img,
  .cp_img {
    width: 0px;
  }
  h1 {
    width: 80%;
  }
}
.mn_title {
  text-align: center;
}
@media (max-width: 650px) {
  .dbskdms {
    font-size: 13px;
  }
  h1 {
    font-size: 20px;
  }
  .pd > div > div > img {
    height: 30%;
  }
  .pd_title {
    font-size: 15px;
  }
  .pd_content {
    font-size: 0px;
  }
  .product,
  .sale {
    height: 250px;
  }
}
.mn_title {
  text-align: center;
}
