.header {
  width: 100%;
  height: 56px;
  background: rgba(255, 255, 255, 0.8);
  border: 0.4px solid #bebebe;
  display: flex;
}

.logo {
  width: 100px;
  height: 30px;
  margin-top: 8.5px;
  margin-right: 13px;
}

.user {
  width: 22px;
  height: 24px;
  right: 20%;
  margin-top: 14px;
  margin-left: 30%;
}

.list {
  width: 20px;
  height: 18px;
  margin: 16px 14px 0 11.8%;
}

.search {
  width: 30%;
  height: 28px;
  border: 2px solid #1e89fd;
  border-radius: 28px;
  background-image: url("../../public/images/search.png");
  background-position: 96% 6px;
  background-repeat: no-repeat;
  background-size: 20px 20px;
  margin-top: 9px;
}

h4 {
  font-weight: 400;
  color: #656667;
  margin-left: 8px;
  margin-top: 15px;
}

@media (max-width: 900px) {
  .user,
  h4 {
    display: none;
  }
  input {
    width: 60%;
  }
}

.bar {
  width: 40px;
  height: 170px;
  background-color: rgba(116, 116, 116, 74%);
  position: absolute;
  top: 9px;
  left: 11.2%;
  border-radius: 7px;
}

.bread {
  width: 30px;
  margin-top: 50px;
  margin-left: 5px;
}
.cookie {
  width: 30px;
  margin-top: 7px;
  margin-left: 5px;
}
.juice {
  width: 25px;
  margin-top: 5px;
  margin-left: 7px;
}
