.section {
  margin-left: 10%;
  display: flex;
  width: 60%;
  margin-top: 20px;
}
.pd {
  width: 400px;
  height: 100px;
  float: left;
  margin-right: 30px;
}
.pd_title,
.pd_content {
  color: black;
  font-weight: 1000;
}
.pd > div > div > img {
  width: 120px;
}
.recommend {
  background: #f8f8f8;
  border: 0.5px solid #000000;
  border-radius: 10px;
  width: 22%;
  height: 300px;
  position: absolute;
  right: 11%;
}
.rm_pd {
  background-color: #3d3d73;
  width: 40%;
  text-align: center;
  align-items: center;
  color: white;
  height: 35px;
  border-radius: 35px;
  margin: 0 auto;
}
.rm_pd > h5 {
  display: flex;
  height: 100%;
  justify-content: center;
  align-items: center;
  /* line-height: 100%; */
  /* font-weight: 300px; */
  /* text-align: center; */
  /* align-items: center; */
}
.num {
  background-color: #4297e8;
  width: 30px;
  height: 30px;
  color: white;
  border-radius: 5px;
  text-align: center;
}
.lank {
  display: flex;
  margin: 0 auto;
  width: 80%;
  height: 30px;
  margin-top: 20px;
}
.lank > h5 {
  height: 30px;
  margin-top: 5px;
  margin-left: 10px;
}
.num > h5 {
  margin-top: 8px;
  font-family: "GmarketSansMedium";
}
@media (max-width: 900px) {
  .recommend {
    display: none;
  }
  .section {
    width: 80%;
  }
}
